<template>
  <div>
    <mt-header :title="tabs[activeTab]">
      <img src="~@/assets/avatar.png" slot="right" class="avatar" @click="sheetVisible = !sheetVisible">
    </mt-header>
    <mt-actionsheet
      :actions="actions"
      v-model="sheetVisible">
    </mt-actionsheet>
    <el-tabs v-model="activeTab">
      <el-tab-pane name="manageClerk" label="" class="pane_container">
        <el-table :data="clerks.clerk" stripe align='left'>
          <el-table-column prop="clerkId" label="">
            <template scope="scope">
              <span class="ellipsis_column">{{scope.row.clerkId}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="password" label="">
            <template scope="scope">
              <span class="ellipsis_column">{{scope.row.password}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="140">
            <template scope="scope">
              <el-button size="small" @click="clerkForm = Object.assign({}, scope.row)"></el-button>
              <el-button size="small" type="danger" @click="deleteClerk(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="fetchClerks($event - 1, clerks.sizePerPage)"
          :current-page="+clerks.currentPage + 1"
          :total="+clerks.totalSize"
          layout="total, prev, pager, next"
          :page-size="+clerks.sizePerPage"
        ></el-pagination>
        <el-form :model="clerkForm" :rules="clerkFormRule" ref="clerkForm" class="form_container" label-width="90px" label-position="right" >
          <el-form-item label="" prop="clerkId">
            <el-input v-model="clerkForm.clerkId" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password" >
            <el-input  v-model="clerkForm.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitClerkForm"></el-button>
            <el-button type="danger" @click="resetClerkForm"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane name="manageAdmin" label="" class="pane_container">
        <el-table :data="admins.admin" stripe align='left'>
          <el-table-column prop="username" label="">
            <template scope="scope">
              <span class="ellipsis_column">{{scope.row.username}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="password" label="">
            <template scope="scope">
              <span class="ellipsis_column">{{scope.row.password}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="140">
            <template scope="scope">
              <el-button size="small" @click="adminForm = Object.assign({}, scope.row)"></el-button>
              <el-button size="small" type="danger" @click="deleteAdmin(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="fetchAdmins($event - 1, admins.sizePerPage)"
          :current-page="+admins.currentPage + 1"
          :total="+admins.totalSize"
          layout="total, prev, pager, next"
          :page-size="+admins.sizePerPage"
        ></el-pagination>
        <el-form :model="adminForm" :rules="adminFormRule" ref="adminForm" class="form_container" label-width="90px" label-position="right" >
          <el-form-item label="" prop="username">
            <el-input v-model="adminForm.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password" >
            <el-input  v-model="adminForm.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitAdminForm"></el-button>
            <el-button type="danger" @click="resetAdminForm"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane name="manageInsurance" label="">
        <div>
          <el-input placeholder="" v-model="queryString">
            <el-select v-model="isQueryClerk" slot="prepend" placeholder="" style="width: 100px;">
              <el-option label="" :value="true"></el-option>
              <el-option label="" :value="false"></el-option>
            </el-select>
            <el-button slot="append" icon="search" @click="insuranceQuery"></el-button>
          </el-input>
        </div>
        <el-table :data="insurances.insurance" align="left" stripe style="margin-top: 15px">
          <el-table-column type="expand" >
            <template scope="scope">
              <el-form label-position="left" inline class="expand_insurance_form">
                <el-form-item label="">
                  <span>{{ scope.row.info.polName}}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.money }} </span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.birthDate }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.sex }} / {{ scope.row.info.sex === 'M' ? '' : '' }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.clerk }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.mobile }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.info.inforceTime }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.insureds.insured[0].rel}}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.insureds.insured[0].insureName}}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.insureds.insured[1].rel}}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ scope.row.insureds.insured[1].insureName}}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="info.baodanNo" label=""></el-table-column>
          <el-table-column prop="info.holderName" width="90" label="">
            <template scope="scope">
              <span class="ellipsis_column">{{scope.row.info.holderName}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="140" >
            <template scope="scope" >
              <el-button size="small" @click="handleEdit(scope.row)"></el-button>
              <el-button size="small" type="danger" @click="handleDelete(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="fetchInsurances({page: $event - 1, size: insurances.sizePerPage, isQueryClerk, queryString})"
          :current-page="+insurances.currentPage + 1"
          :total="+insurances.totalSize"
          layout="total, prev, pager, next"
          :page-size="+insurances.sizePerPage"
        ></el-pagination>
        <transition name="fadeLeft" >
          <el-form
            :model="updateInsuranceForm"
            :rules="updateInsuranceRule"
            ref="updateInsuranceForm"
            label-width="90px"
            class="form_container"
            style="margin-left: 20px; margin-right: 20px;"
            v-show="editMode"
            label-position="right">
            <el-form-item label="" prop="holderName" >
              <el-input v-model="updateInsuranceForm.holderName" placeholder="" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="sex">
              <el-radio-group v-model="updateInsuranceForm.sex">
                <el-radio label="F"></el-radio>
                <el-radio label="M"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="birthDate">
              <el-date-picker v-model="updateInsuranceForm.birthDate" type="date" placeholder="" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="inforceTime">
              <el-date-picker v-model="updateInsuranceForm.inforceTime" type="date" placeholder="" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="mobile">
              <el-input v-model="updateInsuranceForm.mobile" type="number" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="polName">
              <el-select v-model="updateInsuranceForm.polName" placeholder="" style="width: 100%;" clearable>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="clerk">
              <el-select v-model="updateInsuranceForm.clerk" placeholder="" style="width: 100%;" clearable>
                <el-option v-for="(clerk,index) in clerks.clerk" :label="clerk.clerkId" :value="clerk.clerkId" :key="index"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" prop="money">
              <el-input type="number" placeholder="" v-model="updateInsuranceForm.money">
                <template slot="append"></template>
              </el-input>
            </el-form-item>
            <el-form-item label="1" prop="insuredRel1">
              <el-input v-model="updateInsuranceForm.insuredRel1" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="insuredName1">
              <el-input v-model="updateInsuranceForm.insuredName1" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="2" prop="insuredRel2">
              <el-input v-model="updateInsuranceForm.insuredRel2" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="insuredName2">
              <el-input v-model="updateInsuranceForm.insuredName2" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updateInsuranceSubmit"></el-button>
              <el-button type="danger" @click="resetUpdateInsuranceForm"></el-button>
            </el-form-item>
          </el-form>

        </transition>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import request from '@/utils/request'
  import moment from 'moment'
  export default {
    data() {
      return {
        // 
        tabs: {
          manageClerk: '',
          manageAdmin: '',
          manageInsurance: ''

        },
        activeTab: 'manageAdmin',

        // actionsheet
        sheetVisible: false,
        actions: [{
          name: `: ${localStorage.getItem('adminId')}?`,
          method: () => {
            this.logout()
          }
        }],

        // 
        clerks: {
          currentPage: '0',
          sizePerPage: '10',
          totalPages: '1',
          totalSize: '0',
          clerk: []
        },
        clerkForm: {
          clerkId: '',
          password: ''
        },
        clerkFormRule: {
          clerkId: {
            required: true, message: '', trigger: 'blur'
          },
          password: {
            required: true, message: '', trigger: 'blur'
          }
        },

        // 
        admins: {
          currentPage: '0',
          sizePerPage: '10',
          totalPages: '1',
          totalSize: '0',
          admin: []
        },
        adminForm: {
          username: '',
          password: ''
        },
        adminFormRule: {
          username: {
            required: true, message: '', trigger: 'blur'
          },
          password: {
            required: true, message: '', trigger: 'blur'
          }
        },

        // 
        isQueryClerk: true,  // id
        queryString: '', // 
        queryMode: false, // 
        editMode: false, // 
        insurances: {
          currentPage: '0',
          sizePerPage: '10',
          totalPages: '1',
          totalSize: '0',
          insurance: []
        },
        updateInsuranceForm: {
          holderName: '',
          sex: 'F',
          birthDate: '',
          inforceTime: '',
          clerk: '',
          mobile: '',
          polName: '',
          money: NaN,
          insuredRel1: '',
          insuredName1: '',
          insuredRel2: '',
          insuredName2: ''
        }, // 
        updateInsuranceRule: {
          holderName: [{
            required: true, message: '', trigger: 'blur'
          }],
          sex: [{
            required: true, message: '', trigger: 'blur'
          }],
          birthDate: [{
            required: true, type: 'date', message: '', trigger: 'blur'
          }],
          inforceTime: [{
            required: true, type: 'date', message: '', trigger: 'blur'
          }],
          clerk: [{
            required: true, message: '', trigger: 'change'
          }],
          mobile: [{
            required: true, message: '', trigger: 'blur'
          }, {
            pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur'
          }],
          polName: [{
            required: true, message: '', trigger: 'change'
          }],
          money: [{
            required: true, message: '', trigger: 'blur'
          }],
          insuredRel1: [{
            required: true, message: '', trigger: 'blur'
          }],
          insuredName1: [{
            required: true, message: '', trigger: 'blur'
          }],
          insuredRel2: [{
            required: true, message: '', trigger: 'blur'
          }],
          insuredName2: [{
            required: true, message: '', trigger: 'blur'
          }],
        },


      }
    },
    methods: {
      logout() {
        localStorage.clear()
        this.$router.replace('/')
      },


      // clerk
      fetchClerks(page, size) {
        request().get(`clerks?page=${page}&size=${size}`).then(res => {
          console.log(res.data)
          this.clerks = res.data.clerkList
        }).catch(err => {
          console.error(err)
        })
      },
      deleteClerk(id) {
        request().delete(`/clerks/${id}`).then(res => {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          })
          this.fetchClerks(this.clerks.currentPage, this.clerks.sizePerPage)

        }).catch(err => {
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          })
        })
      },
      submitClerkForm() {
        this.$refs.clerkForm.validate(valid => {
          if (valid) {
            let data = this.clerkForm
            let id = data.id
            if (!!id) {
              // 
              request().put(`/clerks/${id}`, {
                clerk: {
                  clerkId: data.clerkId,
                  password: data.password
                }
              }).then(res => {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                })
                this.resetClerkForm()
                this.fetchClerks(this.clerks.currentPage, this.clerks.sizePerPage)

              }).catch(err => {
                let errorMessage = err.response.data.error.message;
                if (/INDEX.*CLERK_ID/.test(errorMessage)) {
                  errorMessage = ''
                }else {
                  errorMessage = ''
                }
                this.$notify({
                  title: '',
                  message: errorMessage,
                  type: 'error',
                  duration: 2000
                })
              })
            } else {
              // 
              request().post('/clerks', {
                clerk: {
                  clerkId: data.clerkId,
                  password: data.password
                }
              }).then(res => {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                })
                this.resetClerkForm()
                this.fetchClerks(this.clerks.currentPage, this.clerks.sizePerPage)

              }).catch(err => {
                let errorMessage = err.response.data.error.message;
                if (/INDEX.*CLERK_ID/.test(errorMessage)) {
                  errorMessage = ''
                }else {
                  errorMessage = ''
                }
                this.$notify({
                  title: '',
                  message: errorMessage,
                  type: 'error',
                  duration: 2000
                })
              })
            }
          }else {
            console.log("error submit")
            return
          }
        })
      },
      resetClerkForm(){
        this.$refs.clerkForm.resetFields()
        this.clerkForm = {
          clerkId: '',
          password: ''
        }
      },

      // admin
      fetchAdmins(page, size) {
        request().get(`admins?page=${page}&size=${size}`).then(res => {
          console.log(res.data)
          this.admins = res.data.adminList
        }).catch(err => {
          console.error(err)
        })
      },
      deleteAdmin(id) {
        request().delete(`/admins/${id}`).then(res => {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          })
          this.fetchAdmins(this.admins.currentPage, this.admins.sizePerPage)

        }).catch(err => {
          this.$notify({
            title: '',
            message: '',
            type: 'error',
            duration: 2000
          })
        })
      },
      submitAdminForm() {
        this.$refs.adminForm.validate(valid => {
          if (valid) {
            let data = this.adminForm
            let id = data.id
            if (!!id) {
              // 
              request().put(`/admins/${id}`, {
                admin: {
                  username: data.username,
                  password: data.password
                }
              }).then(res => {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                })
                this.resetAdminForm()
                this.fetchAdmins(this.admins.currentPage, this.admins.sizePerPage)


              }).catch(err => {
                let errorMessage = err.response.data.error.message;
                if (/INDEX.*USERNAME/.test(errorMessage)) {
                  errorMessage = ''
                }else {
                  errorMessage = ''
                }
                this.$notify({
                  title: '',
                  message: errorMessage,
                  type: 'error',
                  duration: 2000
                })
              })
            } else {
              // 
              request().post('/admins', {
                admin: {
                  username: data.username,
                  password: data.password
                }
              }).then(res => {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                })
                this.resetAdminForm()
                this.fetchAdmins(this.admins.currentPage, this.admins.sizePerPage)


              }).catch(err => {
                let errorMessage = err.response.data.error.message;
                if (/INDEX.*USERNAME/.test(errorMessage)) {
                  errorMessage = ''
                }else {
                  errorMessage = ''
                }
                this.$notify({
                  title: '',
                  message: errorMessage,
                  type: 'error',
                  duration: 2000
                })
              })
            }
          }else {
            console.log("error submit")
            return
          }
        })
      },
      resetAdminForm(){
        this.$refs.adminForm.resetFields()
        this.adminForm = {
          username: '',
          password: ''
        }
      },

      // insurance
      fetchInsurances({page, size, isQueryClerk, queryString}) {
        if (!queryString) {
          // 
          request().get(`/insurances?page=${page}&size=${size}`).then(res => {
            console.log(res.data)
            this.insurances = res.data.insurances
          }).catch(err => {
            console.error(err)
          })
        }else {
          // 
          if (isQueryClerk) {
            // 
            request().get(`/insurances/query?clerk=${queryString}&page=${page}&size=${size}`).then(res => {
              console.log(res.data)
              this.insurances = res.data.insurances
            }).catch(err => {
              console.error(err)
            })
          } else {
            // 
            request().get(`/insurances/query?holder=${queryString}&page=${page}&size=${size}`).then(res => {
              console.log(res.data)
              this.insurances = res.data.insurances
            }).catch(err => {
              console.error(err)
            })
          }
        }
      },
      updateInsuranceSubmit() {
        this.$refs.updateInsuranceForm.validate(valid => {
          if (valid) {
            let data = this.updateInsuranceForm
            if(!!data.id) {
              // 
              console.log('')
              request().put(`/insurances/${data.id}`, {
                root: {
                  info: {
                    id: data.insurance.info.id,
                    holderName: data.holderName,
                    sex: data.sex,
                    birthDate: moment(data.birthDate).format('YYYY-MM-DD'),
                    mobile: data.mobile,
                    polName: data.polName,
                    money: data.money,
                    inforceTime: moment(data.inforceTime).format('YYYY-MM-DD'),
                    clerk: data.clerk,
                    baodanNo: data.insurance.info.baodanNo
                  },
                  insureds: {
                    insured: [
                      {
                        id: data.insurance.insureds.insured[0].id,
                        rel: data.insuredRel1,
                        insureName: data.insuredName1
                      }, {
                        id: data.insurance.insureds.insured[1].id,
                        rel: data.insuredRel2,
                        insureName: data.insuredName2
                      }
                    ]
                  }
                }
              }).then(res => {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                });
                this.resetUpdateInsuranceForm()
                this.fetchInsurances({
                  page: this.insurances.currentPage,
                  size: this.insurances.sizePerPage,
                  isQueryClerk: this.isQueryClerk,
                  queryString: this.queryMode ? this.queryString : ''
                })

                console.log(res.data)
              }).catch(err => {
                console.error(err)
              })
            }
          }else {
            console.log('error submit')
            return false
          }
        })
      },
      insuranceQuery(){
        this.fetchInsurances({
          page: this.insurances.currentPage,
          size: this.insurances.sizePerPage,
          isQueryClerk: this.isQueryClerk,
          queryString: this.queryMode ? this.queryString : ''
        })
      },
      resetUpdateInsuranceForm(){
        this.$refs.updateInsuranceForm.resetFields()
        this.updateInsuranceForm = {
          holderName: '',
          sex: 'F',
          birthDate: '',
          mobile: '',
          polName: '',
          money: NaN,
          insuredRel1: '',
          insuredName1: '',
          insuredRel2: '',
          insuredName2: '',
          inforceTime: '',
          clerk: '',
        }
        this.editMode = false

      },
      handleEdit(data) {
        console.log(data)
        this.updateInsuranceForm = {
          id: data.id,
          holderName: data.info.holderName,
          sex: data.info.sex,
          birthDate: moment(data.info.birthDate).toDate(),
          mobile: data.info.mobile,
          polName: data.info.polName,
          money: data.info.money,
          clerk: data.info.clerk,
          inforceTime: moment(data.info.inforceTime).toDate(),
          insuredRel1: data.insureds.insured[0].rel,
          insuredName1: data.insureds.insured[0].insureName,
          insuredRel2: data.insureds.insured[1].rel,
          insuredName2: data.insureds.insured[1].insureName,
          insurance: data
        }
        this.editMode = true

      },
      handleDelete(id) {
        console.log(id)
        request().delete(`/insurances/${id}`).then(res => {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            duration: 2000
          });
          this.fetchInsurances({
            page: this.insurances.currentPage,
            size: this.insurances.sizePerPage,
            isQueryClerk: this.isQueryClerk,
            queryString: this.queryMode ? this.queryString : ''
          })
        })
      },
    },
    watch: {
      queryString:function(input){
        this.queryMode = input !== ''
      }
    },
    created() {
      this.fetchClerks(this.clerks.currentPage, this.clerks.sizePerPage)
      this.fetchAdmins(this.admins.currentPage, this.admins.sizePerPage)
      this.fetchInsurances({
        page: this.insurances.currentPage,
        size: this.insurances.sizePerPage,
        isQueryClerk: this.isQueryClerk,
        queryString: this.queryMode ? this.queryString : ''
      })
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .pane_container {
    padding: 0 20px;
    text-align: center;
  }
  .form_container {
    text-align: left;
    margin: 20px 0;
    padding: 20px 10px;
    box-shadow: lightgrey 0 1px 10px;
  }
</style>



